.c-button {
  white-space: nowrap;
  width: 100%;
  line-height: 3;
  color: white;
  background-color: rgb(179, 179, 179);
  border-radius: 0.25rem
}
.c-button:active {
  background-color: rgb(204, 204, 204)
}
.c-button.x-large {
  line-height: 4.2
}
.c-button.large {
  line-height: 3.6
}
.c-button.small {
  line-height: 2.4
}
.c-button.x-small {
  line-height: 1.8
}
.c-button.primary {
  background-color: #39b983
}
.c-button.primary:active {
  background-color: rgb(87, 203, 155)
}
.c-button.primary:disabled {
  cursor: not-allowed;
  color: rgb(179, 179, 179);
  background-color: rgb(204, 204, 204)
}
.c-button.warning {
  background-color: #BA3A3A
}
.c-button.warning:active {
  background-color: rgb(204, 92, 92)
}
.c-button.warning:disabled {
  cursor: not-allowed;
  color: rgb(179, 179, 179);
  background-color: rgb(204, 204, 204)
}
.c-button.default {
  color: rgb(51, 51, 51);
  background-color: rgb(204, 204, 204)
}
.c-button.default:active {
  background-color: rgb(230, 230, 230)
}
.c-button.default:disabled {
  cursor: not-allowed;
  color: rgb(179, 179, 179);
  background-color: rgb(204, 204, 204)
}



