html {
  font: 1em/1.5 Tahoma, Arial, Helvetica, "Microsoft YaHei", "宋体", sans-serif;
}

body {
  font-size: 1rem;
  color: rgb(51, 51, 51);
  background-color: #FBF9FE;
}

body, h1, h2, h3, h4, h5, h6, p, dl, dd, form, ul, ol, pre, blockquote, textarea, input, figure, button {
  margin: 0;
}

ul, ol, th, td, button, textarea, input {
  padding: 0;
}

input, textarea, keygen, select, button {
  font: 1rem/1.5 Tahoma, Arial, Helvetica, "Microsoft YaHei", "宋体", sans-serif;
  color: rgb(51, 51, 51);
}

button, input, textarea {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

ul, ol {
  list-style: none;
}

code, kbd, pre, samp {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
}

pre code {
  padding: 0;
  font-size: inherit;
  color: inherit;
  white-space: pre-wrap;
  background-color: transparent;
  border-radius: 0;
}

a {
  color: #39b983;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent
}

a:hover {
  text-decoration: none;
}

a:active {
  color: #34495E;
}

article, footer, header, nav, section {
  display: block;
}

@font-face {
  font-family: 'iconfont';
  src: url('fonts/iconfont.ttf') format('truetype');
}

.container {
  padding: 4rem 0 0;
}

.header {
  position: fixed;
  z-index: 11;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background-color: #34495E;
}

.history {
}

.history .c-button {
  position: absolute;
  z-index: 1;
  width: 4rem;
  height: 4rem;
  background-color: transparent;
}

.history .c-button:active {
  color: #39b983;
  background-color: transparent;
}

.history .c-route-link {
  color: white;
}

.logo {
  position: absolute;
  z-index: 1;
  width: 100%
  /*background-color: inherit;*/
}

.logo a {
  display: block;
  height: 4rem;
  color: white;
  font-size: 1.2em;
  line-height: 4rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
}

.logo .link-active {
  color: white;
}

.router-view {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}

.router-view-transition-enter,
.router-view-transition-leave {
  opacity: 0;
  -webkit-transform: translate3d(0, 1rem, 0);
          transform: translate3d(0, 1rem, 0);
}

[v-cloak] {
  display: none;
}

