  color: var(--secondary);

  &:active {
    color: var(--primary);
  }

  &:disabled {
    cursor: not-allowed;
    color: color(var(--primary) saturation(0%));
  }
}*/

.link-active {
  color: #39b983;
}

.slide-transition {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}

.slide-enter, .slide-leave {
  opacity: 0;
  -webkit-transform: translate3d(0, -1rem, 0);
          transform: translate3d(0, -1rem, 0);
}

.slide-up-transition {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}

.slide-up-enter, .slide-up-leave {
  opacity: 0;
  -webkit-transform: translate3d(0, 1rem, 0);
          transform: translate3d(0, 1rem, 0);
}

.fade-transition {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}

.fade-enter, .fade-leave {
  opacity: 0;
}



